import React, { useContext, useMemo } from 'react';
import { AppContext } from 'components/common/context/AppProvider';
import ConstellationsComp from 'components/account/Misc/Constellations';
import StarSigns from 'components/account/Misc/StarSigns';
import { NextSeo } from 'next-seo';
import { getShinyBonus } from '../../../parsers/breeding';
import { isRiftBonusUnlocked } from '../../../parsers/world-4/rift';
import Tabber from '../../../components/common/Tabber';
const tabs = ['Constellations', 'Star Signs'];
const Constellations = () => {
  const {
    state
  } = useContext(AppContext);
  const getInfiniteStar = (rift, pets) => {
    if (isRiftBonusUnlocked(rift, 'Infinite_Stars')) {
      return 5 + getShinyBonus(pets, 'Infinite_Star_Signs');
    }
  };
  const sortStarSigns = starSigns => {
    const sortAlphaNum = (a, b) => a.indexedStarName.localeCompare(b.indexedStarName, 'en', {
      numeric: true
    });
    const sortedSigns = starSigns?.sort(sortAlphaNum);
    const lastItem = sortedSigns?.pop();
    sortedSigns?.splice(21, 0, lastItem);
    return sortedSigns;
  };
  const infiniteStars = useMemo(() => getInfiniteStar(state?.account?.rift, state?.account?.breeding?.pets), [state?.account?.rift, state?.account?.breeding?.pets]);
  const stars = useMemo(() => sortStarSigns(state?.account?.starSigns), [state?.account?.starSigns]);
  return <div data-sentry-component="Constellations" data-sentry-source-file="constellations.jsx">
    <NextSeo title="Constellations | Idleon Toolbox" description="Constellation and star signs overview" data-sentry-element="NextSeo" data-sentry-source-file="constellations.jsx" />
    <Tabber tabs={tabs} clearOnChange={['nt']} data-sentry-element="Tabber" data-sentry-source-file="constellations.jsx">
      <ConstellationsComp constellations={state?.account?.constellations} data-sentry-element="ConstellationsComp" data-sentry-source-file="constellations.jsx" />
      <StarSigns starSigns={stars} infiniteStars={infiniteStars} data-sentry-element="StarSigns" data-sentry-source-file="constellations.jsx" />
    </Tabber>
  </div>;
};
export default Constellations;